import axios from 'axios';
import Vue from 'vue';
import router from './router.js';
import vm from '../main.js';
//
axios.interceptors.request.use(function(config) {
  //config   axios({})
  
  // token
  let user = window.localStorage.getItem('user');//"{err:0,msg:'xx',data:{},token:'xx'}"
  let token = user ? JSON.parse(user).token : '';
  
  config.headers={
    // token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VybmFtZSI6InlvdXlvdSIsIl9pZCI6IjVmMWU1MzdkYmUwYzIyMDY0ODI3MjM3YiIsImlhdCI6MTU5Njc2OTgwNywiZXhwIjoxNTk2ODU2MjA3fQ.q6fjl4r1blDJtfx-u4ljHYPcIFcW7OqT04tGFOz9HLk'
    token
  }
  //loading
  vm.bLoading = true;
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
  //response  ~~ axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  
  if(response.data.err===2 && !currentRoute.includes('/login')){
    router.replace({
      path:'/login',
      query:{p:currentRoute}
    })
  }
  // loading
  vm.bLoading = false;
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;//vueaxiosVueaxios$axios
window.axios=axios;//axios
export default axios;//axios